import { Component, OnInit } from '@angular/core';
import { AssetModelCategoryMasterService, AssetModelCategoryService } from 'src/app/sdk/asset/services';
import { query } from '@angular/animations';
import { Model_category_master, Model_category } from 'src/app/sdk/asset/models';
import { ModelDetailService } from '../../model-detail.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-category',
    templateUrl: './category.component.html',
    styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

    constructor(
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private assetModelCategoryMasterService: AssetModelCategoryMasterService,
        private AssetModelCategoryService: AssetModelCategoryService,
        private modelDetailService: ModelDetailService,
        private router: Router
    ) { }

    id_model: string = null;


    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    this.getCategory()
                }
            }
        )

    }

    listCategory: any[] = []

    id_model_category_master: string = ''
    getCategory() {
        this.assetModelCategoryMasterService.getAssetModelCategoryMasters().subscribe(
            data => {
                let out = [];
                data.data.forEach(item => {

                    let param: any = item;

                    if (param.id_model_category_master == this.id_model_category_master) {
                        param.id_model_category_master = true;
                    } else (
                        param.id_model_category_master = false
                    )
                    out.push(param);

                })
                this.listCategory = out;
            }
        )
    }

    postCategory(data) {
        if (data.id_model_category_master == true) {
            data.id_model_category_master = data.id_model_category_master;
        } else {
            data.id_model_category_master = null
        }
        let param: Model_category = {
            id_model_category_master: data.id_model_category_master,
            id_model: this.id_model,
        }

        this.spinner.show()
        this.AssetModelCategoryService.postAssetModelCategories(param).subscribe(
            data => {
                this.spinner.hide();

            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('gagal di update');
            }
        )
    }

}
