import { NgxSpinnerService } from 'ngx-spinner';
import { Component, OnInit } from '@angular/core';
import { AssetPartIdService, AssetLifeLimitService, AssetConditionMonitoringService, AssetModelxService, VendorVendorService } from 'src/app/sdk/asset/services';
import { Router, ActivatedRoute } from '@angular/router';
import { Part_id, Life_limit, Condition_monitoring, Modelx, Vendor } from 'src/app/sdk/asset/models';
import { AssetModelListService } from 'src/app/sdk/asset/services/asset-model-list.service';
import { ModelDetailService } from '../model-detail.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private assetPartIdService: AssetPartIdService,
        private assetLifeLimitService: AssetLifeLimitService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,
        private vendorVendorService: VendorVendorService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private modelDetailService: ModelDetailService,
        private assetModelListService: AssetModelListService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_model: string = ''
    dataModel: Modelx = {}
    ngOnInit() {

        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    this.getModel()
                }
            }
        )

    }


    getModel() {
        this.spinner.show()
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.dataModel = data.data;
                this.spinner.hide()

                this.getPartId(this.dataModel.id_part_id)
                this.getConditionMonitoring(this.dataModel.id_condition_monitoring)
                this.getLifeLimit(this.dataModel.id_life_limit)
                this.getVendor(this.dataModel.id_manufacture)

            }
        )
    }

    dataType: Part_id = {};
    getPartId(id) {
        this.spinner.show()
        this.assetPartIdService.getAssetPartIdsId(id).subscribe(
            data => {
                this.dataType = data.data
                this.spinner.hide()
            }
        )
    }

    dataLifelimit: Life_limit = {};
    getLifeLimit(id) {
        this.spinner.show()
        this.assetLifeLimitService.getAssetLifeLimitsId(id).subscribe(
            data => {
                this.dataLifelimit = data.data
                this.spinner.hide()
            }
        )
    }

    dataCondition: Condition_monitoring = {};
    getConditionMonitoring(id) {
        this.spinner.show()
        this.assetConditionMonitoringService.getAssetConditionMonitoringsId(id).subscribe(
            data => {
                this.dataCondition = data.data
                this.spinner.hide()
            }
        )
    }

    dataVendor: Vendor = {};
    getVendor(id) {
        this.spinner.show()
        this.vendorVendorService.getVendorVendorsId(id).subscribe(
            data => {
                this.dataVendor = data.data
                this.spinner.hide()
            }
        )
    }

}
