import { Modelx } from 'src/app/sdk/asset/models';
import { AssetModelxService } from 'src/app/sdk/asset/services';
import { ModelDetailService } from './../model-detail.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Pegawai } from 'src/app/sdk/hr/models';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-foto',
  templateUrl: './foto.component.html',
  styleUrls: ['./foto.component.scss']
})
export class FotoComponent implements OnInit {

    constructor(
        private assetModelxService: AssetModelxService,
        private modelDetailService: ModelDetailService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) { }

    id_model: string = ''
    toogleUpload = false

    dataModel: Modelx = {}
    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(data => {
            this.id_model = data
            console.log('id pegawai paja tu ' + this.id_model)
        })
        this.getData()
        this.form()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ foto: data })
                    this.simpan()
                }
            }
        )
    }

    // togleShowUpload() {

    //     this.toogleUpload = true;
    // }
    foto = 'https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png'
    getData() {
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {

                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "asset_model",
                    for_table: "erp_asset.model",
                    for_module_id: data.data.id_model
                })

                // if (data.data.foto) {
                //     this.foto = "http://erp-document.vm.devetek.com/download?id_document=" + data.data.foto
                // }

            }
        )
    }

    dataForm: FormGroupTypeSafe<Modelx>;
    form() {
        this.dataForm = this.fb.group<Modelx>(
            {
                id_manufacture: new FormControl('', [Validators.required]),
                id_condition_monitoring: new FormControl('', [Validators.required]),
                id_life_limit: new FormControl('', [Validators.required]),
                id_part_id: new FormControl('', [Validators.required]),
                part_number: new FormControl(''),
                keyword: new FormControl(''),
                life_limit_var: new FormControl(''),
                life_limit_val: new FormControl(''),
                min_qty: new FormControl(''),
                unit: new FormControl(''),
                id_user: new FormControl(''),
                id_model: new FormControl(''),
                is_single: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                foto: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataModel = this.dataForm.value;
        this.assetModelxService.putAssetModelxesId({ id: this.id_model, body: this.dataModel }).subscribe(
            data => {
                this.toogleUpload = false
                this.getData()
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }


    goToBack() {
        this.router.navigate(['/asset/model', this.id_model, 'view','detail'])
    }

}
