import { Component, OnInit } from '@angular/core';
import { AssetModelTemplateChildService, AssetModelxService } from 'src/app/sdk/asset/services';
import { ModelDetailService } from '../model-detail.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Model_template_child } from 'src/app/sdk/asset/models';
import { SearchItemComponent } from 'src/app/components/search-item/search-item/search-item.component';

@Component({
    selector: 'app-license',
    templateUrl: './license.component.html',
    styleUrls: ['./license.component.scss']
})
export class LicenseComponent implements OnInit {

    constructor(
        private assetModelTemplateChildService: AssetModelTemplateChildService,
        private modelDetailService: ModelDetailService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private drawerService: NzDrawerService
    ) { }


    listModelChild: Model_template_child[] = []
    id_model: string = null
    id_model_child: string = null

    id_part_id: string = 'license'

    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    console.log('sadasda ' + this.id_model)
                    this.getlistChild()
                }
            }
        )

    }

    add_item() {
        const drawerRef = this.drawerService.create<SearchItemComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearchItemComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_model_child = data;
                this.beforeModel()
            }
        });
    }


    getlistChild() {
        let param = {
            id_model_child: this.id_model,
            id_part_id: this.id_part_id
        }
        this.assetModelTemplateChildService.getAssetModelTemplateChildren(JSON.stringify(param)).subscribe(
            data => {
                this.listModelChild = data.data
            }
        )
    }


    deleteItem(id) {
        this.spinner.show()
        this.assetModelTemplateChildService.deleteAssetModelTemplateChildrenId(id).subscribe(
            data => {
                this.getlistChild()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};

    beforeModel() {
        console.log("panggie ko")
        this.spinner.show()
        this.assetModelxService.getAssetModelxesId(this.id_model_child).subscribe(
            data => {
                this.dataItem.keyword = data.data.keyword;
                this.dataItem.part_number = data.data.part_number;
                this.dataItem.id_model_child = data.data.id_model;
                this.dataItem.life_limit_var = data.data.life_limit_var;
                this.dataItem.life_limit_var = data.data.life_limit_val;
                this.dataItem.id_model_parent = this.id_model;
                this.spinner.hide()


                console.log(this.dataItem)

            }
        )
    }

    simpan_item() {

        let param: Model_template_child = {
            id_model_child: this.id_model,
            id_model: this.id_model_child
        };

        this.spinner.show()
        //simpan ka server
        this.assetModelTemplateChildService.postAssetModelTemplateChildren(param).subscribe(
            data => {
                this.id_model_child = null;
                this.spinner.hide();
                this.getlistChild()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

}
