import { Component, OnInit, Input } from '@angular/core';
import { Maintenance_code } from 'src/app/sdk/asset/models';
import { AssetMaintenanceCodeService } from 'src/app/sdk/asset/services';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
  selector: 'app-maintenance-code',
  templateUrl: './maintenance-code.component.html',
  styleUrls: ['./maintenance-code.component.scss']
})
export class MaintenanceCodeComponent implements OnInit {

    @Input() value = '';

    ngOnInit(): void {

    }

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private asssetMaintenanceCodeService : AssetMaintenanceCodeService
    ) { }

    close(): void {
        this.drawerRef.close(this.value);
    }

    maintenanceCode: Maintenance_code = {}
    addMaintenanceCode() {

        this.spinner.show();
        this.asssetMaintenanceCodeService.postAssetMaintenanceCodes(this.maintenanceCode).subscribe(
            data => {
                this.spinner.hide()
                this.close()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Maintenance Code sudah ada');
            }
        )
    }

}
