import { Component, OnInit, Input } from '@angular/core';
import { AssetModelLifeService } from 'src/app/sdk/asset/services';
import { Model_life } from 'src/app/sdk/asset/models';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
  selector: 'app-model-life',
  templateUrl: './model-life.component.html',
  styleUrls: ['./model-life.component.scss']
})
export class ModelLifeComponent implements OnInit {

    @Input() value = '';

    ngOnInit(): void {

    }

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private assetModelLifeService : AssetModelLifeService
    ) { }

    close(): void {
        this.drawerRef.close(this.value);
    }

    modelLife: Model_life = {}
    addModelLife() {

        this.spinner.show();
        this.assetModelLifeService.postAssetModelLives(this.modelLife).subscribe(
            data => {
                this.spinner.hide()
                this.close()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Model life sudah ada');
            }
        )
    }

}
