import { StockComponent } from './stock/stock.component';
import { PembelianComponent } from './riwayat/pembelian/pembelian.component';
import { ModelDetailComponent } from './model-detail.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DetailComponent } from './detail/detail.component';
import { SpesificationComponent } from './spesification/spesification.component';
import { ComponenComponent } from './componen/componen.component';
import { MaintenanceComponent } from './maintenance/maintenance.component';
import { ConsumsiableComponent } from './consumsiable/consumsiable.component';
import { LicenseComponent } from './license/license.component';
import { ViewComponent } from './view/view.component';
import { CategoryComponent } from './pengaturan/category/category.component';
import { OwnerComponent } from './pengaturan/owner/owner.component';
import { PermintaanComponent } from './riwayat/permintaan/permintaan.component';
import { FotoComponent } from './foto/foto.component';

const routes: Routes = [
    {
        path: '',
        component: ModelDetailComponent,
        children: [
            {
                path: '',
                redirectTo: 'view'
            },
            {
                path: 'maintenance',
                component: MaintenanceComponent
            },
            {
                path: 'stock',
                component: StockComponent
            },
            {
                path: 'foto',
                component: FotoComponent
            },
            {
                path: 'riwayat/pembelian',
                component: PembelianComponent
            },
            {
                path: 'riwayat/permintaan',
                component: PermintaanComponent
            },
            {
                path: 'pengaturan/category',
                component: CategoryComponent
            },
            {
                path: 'pengaturan/owner',
                component: OwnerComponent
            },
            {
                path: 'view',
                component: ViewComponent,
                children: [
                    {
                        path: '',
                        redirectTo: 'detail'
                    },
                    {
                        path: 'detail',
                        component: DetailComponent
                    },
                    {
                        path: 'spesifikasi',
                        component: SpesificationComponent
                    },
                    {
                        path: 'componen',
                        component: ComponenComponent
                    },
                    {
                        path: 'consumsiable',
                        component: ConsumsiableComponent
                    },
                    {
                        path: 'license',
                        component: LicenseComponent
                    },
                ]
            }
        ]

    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ModelDetailRoutingModule { }
