import { Component, OnInit } from '@angular/core';
import { AssetModelOwnerMasterService, AssetModelOwnerService, AssetModelxService } from 'src/app/sdk/asset/services';
import { Model_owner } from 'src/app/sdk/asset/models';
import { ModelDetailService } from '../../model-detail.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { SearcOwnerComponent } from '../searc-owner/searc-owner.component';


@Component({
    selector: 'app-owner',
    templateUrl: './owner.component.html',
    styleUrls: ['./owner.component.scss']
})
export class OwnerComponent implements OnInit {

    constructor(
        private assetModelOwnerMasterService: AssetModelOwnerMasterService,
        private assetModelOwnerService: AssetModelOwnerService,
        private modelDetailService: ModelDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private drawerService: NzDrawerService
    ) { }


    listOwner: Model_owner[] = []
    id_model: string = null


    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    console.log('sadasda ' + this.id_model)
                    this.getlistOwner()
                }
            }
        )

    }

    add_item() {
        const drawerRef = this.drawerService.create<SearcOwnerComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearcOwnerComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_model_owner_master = data;
                this.beforeModel()
            }
        });
    }


    getlistOwner() {
        let param = {
            id_model: this.id_model,
        }
        this.assetModelOwnerService.getAssetModelOwners(JSON.stringify(param)).subscribe(
            data => {
                this.listOwner = data.data
            }
        )
    }


    deleteItem(id) {
        this.spinner.show()
        this.assetModelOwnerService.deleteAssetModelOwnersId(id).subscribe(
            data => {
                this.getlistOwner()
                this.spinner.hide()
            }
        )
    }

    id_model_owner_master: string = ''

    dataItem: any = {};

    beforeModel() {
        console.log("panggie ko")
        this.spinner.show()
        this.assetModelOwnerMasterService.getAssetModelOwnerMastersId(this.id_model_owner_master).subscribe(
            data => {
                this.dataItem.name_model_owner = data.data.name_model_owner;
                this.dataItem.model_owner_desc = data.data.model_owner_desc;
                this.dataItem.id_model_owner_master = data.data.id_model_owner_master;
                this.spinner.hide()


                console.log(this.dataItem)

            }
        )
    }

    simpan_item() {

        let param: Model_owner = {
            id_model: this.id_model,
            id_model_owner_master: this.dataItem.id_model_owner_master
        };

        this.spinner.show()
        //simpan ka server
        this.assetModelOwnerService.postAssetModelOwners(param).subscribe(
            data => {
                this.id_model_owner_master = null;
                this.spinner.hide();
                this.getlistOwner()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

}
