import { NgxSpinnerService } from 'ngx-spinner';
import { Component, OnInit } from '@angular/core';
import { AssetModelxService, AssetAssetService } from 'src/app/sdk/asset/services';
import { ModelDetailService } from '../model-detail.service';

@Component({
    selector: 'app-stock',
    templateUrl: './stock.component.html',
    styleUrls: ['./stock.component.scss']
})
export class StockComponent implements OnInit {

    constructor(
        private assetModelxService: AssetModelxService,
        private assetAssetService: AssetAssetService,
        private spinner: NgxSpinnerService,
        private modelDetailService: ModelDetailService,
    ) { }

    id_model: string = ''
    ngOnInit() {

        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    this.searchData()
                }
            }
        )

    }


    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {

        this.spinner.show()
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.assetAssetService
            .getAssetAssets(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search, id_model: this.id_model
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
                this.spinner.hide()
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }


}
