import { Component, OnInit } from '@angular/core';
import { GaPembelianItemService } from 'src/app/sdk/asset/services';
import { Pembelian_item } from 'src/app/sdk/hr/models';
import { ModelDetailService } from '../../model-detail.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pembelian',
  templateUrl: './pembelian.component.html',
  styleUrls: ['./pembelian.component.scss']
})
export class PembelianComponent implements OnInit {

    constructor(
        private gaPembelianItemService: GaPembelianItemService,
        private modelDetailService: ModelDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
    ) { }


    listPembelian: Pembelian_item[] = []
    id_model: string = ''


    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    console.log('sadasda ' + this.id_model)
                    this.getlistPembelian()
                }
            }
        )

    }



    getlistPembelian() {
        this.gaPembelianItemService.getGaPembelianItems(JSON.stringify({id_model: this.id_model})).subscribe(
            data => {
                this.listPembelian = data.data
            }
        )
    }
}
