import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModelDetailService } from '../model-detail.service';
import { AssetModelxService, AssetCustomFildService, AssetModelSpecificationService, AssetModelCategoryMasterService } from 'src/app/sdk/asset/services';
import { Model_specification, Custom_fild } from 'src/app/sdk/asset/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzDrawerService } from 'ng-zorro-antd';
import { CustomFieldComponent } from '../custom-field/custom-field.component';

@Component({
    selector: 'app-spesification',
    templateUrl: './spesification.component.html',
    styleUrls: ['./spesification.component.scss']
})
export class SpesificationComponent implements OnInit {

    constructor(
        private assetCustomFildService: AssetCustomFildService,
        private assetModelSpecificationService: AssetModelSpecificationService,
        private assetModelxService: AssetModelxService,

        private modelDetailService: ModelDetailService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,

        private drawerService: NzDrawerService

    ) { }

    toogleAdd = false

    id_model: string = null;
    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    this.getSpesifikasi();
                }
            }
        )

        this.getCusstomFiled()
    }

    listCustomField: Custom_fild[] = []
    getCusstomFiled() {
        this.assetCustomFildService.getAssetCustomFilds().subscribe(
            data => {
                this.listCustomField = data.data;
            }
        )
    }

    Add(){
        this.toogleAdd = true
    }

    listSpesification: Model_specification[] = [];
    getSpesifikasi() {
        this.spinner.show()
        this.assetModelSpecificationService.getAssetModelSpecifications(JSON.stringify({ id_model: this.id_model })).subscribe(
            data => {
                this.listSpesification = data.data
                this.spinner.hide();
            }
        )
    }

    is_required: boolean = false;
    value_default = '';
    id_custom_field: string = null;
    urutan: number = 0;
    addSpesification() {
        let params: Model_specification = {
            id_model: this.id_model,
            is_requeired: this.is_required ? 'ya' : 'tidak',
            urutan: this.urutan,
            value_default: this.value_default,
            id_custom_field: this.id_custom_field,
        }
        if (!this.id_custom_field) {
            this.pesan.pesanWarningForm('Field harus di isi!')
            return false;
        }

        this.spinner.show();
        this.assetModelSpecificationService.postAssetModelSpecifications(params).subscribe(
            data => {
                this.spinner.hide()
                this.getSpesifikasi();
                this.is_required = false;
                this.value_default = '-';
                this.id_custom_field = null;
                this.urutan = 0;
                this.toogleAdd = false
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Spesifikasi sudah ada');
            }
        )
    }

    deleteSpesifikasi(id) {
        this.spinner.show()
        this.assetModelSpecificationService.deleteAssetModelSpecificationsId(id).subscribe(
            data => {
                this.spinner.hide();
                this.getSpesifikasi()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('spesifiaasi sudah di gunakan, tidak boleh di delete!')
            }
        )
    }


    // ##FIELD
    openComponent(): void {
        const drawerRef = this.drawerService.create<CustomFieldComponent, { value: string }, string>({
            nzTitle: 'Add new Custom Field',
            nzContent: CustomFieldComponent,
            nzWidth: '320px'
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.getCusstomFiled()
            }
        });
    }
}



