import { Purchase_request_item } from 'src/app/sdk/asset/models';
import { GaPurchaseRequestItemService } from 'src/app/sdk/asset/services';
import { Component, OnInit } from '@angular/core';
import { ModelDetailService } from '../../model-detail.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-permintaan',
  templateUrl: './permintaan.component.html',
  styleUrls: ['./permintaan.component.scss']
})
export class PermintaanComponent implements OnInit {

    constructor(
        private gaPurchaseRequestItemService: GaPurchaseRequestItemService,
        private modelDetailService: ModelDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
    ) { }


    listPurchase: Purchase_request_item[] = []
    id_model: string = ''


    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    console.log('sadasda ' + this.id_model)
                    this.getlistPembelian()
                }
            }
        )

    }



    getlistPembelian() {
        this.gaPurchaseRequestItemService.getGaPurchaseRequestItems(JSON.stringify({id_model: this.id_model})).subscribe(
            data => {
                this.listPurchase = data.data
            }
        )
    }

}
