import { Component, OnInit } from '@angular/core';
import { AssetModelMaintenanceService, AssetModelLifeService, AssetMaintenanceCodeService } from 'src/app/sdk/asset/services';
import { Maintenance_code, Model_life, Model_maintenance } from 'src/app/sdk/asset/models';
import { ModelDetailService } from '../model-detail.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzDrawerService } from 'ng-zorro-antd';
import { MaintenanceCodeComponent } from '../maintenance-code/maintenance-code.component';
import { ModelLifeComponent } from '../model-life/model-life.component';

@Component({
  selector: 'app-maintenance',
  templateUrl: './maintenance.component.html',
  styleUrls: ['./maintenance.component.scss']
})
export class MaintenanceComponent implements OnInit {

    constructor(
        private assetModelMaintenanceService: AssetModelMaintenanceService,
        private assetModelLifeService: AssetModelLifeService,
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,

        private modelDetailService: ModelDetailService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,

        private drawerService: NzDrawerService

    ) { }

    id_model: string = null;
    ngOnInit() {
        this.modelDetailService.getIdModel().subscribe(
            data => {
                if (data) {
                    this.id_model = data
                    this.getMaintenance();
                }
            }
        )

        this.getCode()
        this.getModelLife()
    }

    toogleAdd= false

    add() {
        this.toogleAdd = true
    }

    listMaintenanceCode: Maintenance_code[] = []
    getCode() {
        this.assetMaintenanceCodeService.getAssetMaintenanceCodes().subscribe(
            data => {
                this.listMaintenanceCode = data.data;
            }
        )
    }

    listModellife: Model_life[] = []
    getModelLife() {
        this.assetModelLifeService.getAssetModelLives().subscribe(
            data => {
                this.listModellife = data.data;
            }
        )
    }

    listMaintenance: Model_maintenance[] = [];
    getMaintenance() {
        this.spinner.show()
        this.assetModelMaintenanceService.getAssetModelMaintenances(JSON.stringify({ id_model: this.id_model })).subscribe(
            data => {
                this.listMaintenance = data.data
                this.spinner.hide()
            }
        )
    }

    periode_val: number = 0;
    periode_var: string = ''
    id_maintenance_code: string = null;
    id_model_life: string = null;
    model_life_val: number = 0;

    addMaintenance() {
        let params: Model_maintenance = {
            id_model: this.id_model,
            periode_val: this.periode_val,
            periode_var: this.periode_var,
            id_maintenance_code: this.id_maintenance_code,
            id_model_life: this.id_model_life,
            model_life_val: this.model_life_val,
        }
        if (!this.id_maintenance_code) {
            this.pesan.pesanWarningForm('Field harus di isi!')
            return false;
        }

        this.assetModelMaintenanceService.postAssetModelMaintenances(params).subscribe(
            data => {
                this.getMaintenance();
                this.periode_val = 0;
                this.periode_var = '-';
                this.id_maintenance_code = null;
                this.model_life_val = 0;
                this.id_model_life = '';
                this.toogleAdd = false
            },
            err => {
                this.pesan.pesanWarningForm('Maintenance sudah ada');
            }
        )
    }

    deleteMaintenance(id) {
        this.spinner.show()
        this.assetModelMaintenanceService.deleteAssetModelMaintenancesId(id).subscribe(
            data => {
                this.spinner.hide();
                this.getMaintenance()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('spesifiaasi sudah di gunakan, tidak boleh di delete!')
            }
        )
    }



    openCode(): void {
        const drawerRef = this.drawerService.create<MaintenanceCodeComponent, { value: string }, string>({
            nzTitle: 'Add new Maintenance Code',
            nzContent: MaintenanceCodeComponent,
            nzWidth: '320px'
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.getCode()
            }
        });
    }

    openModelLife(): void {
        const drawerRef = this.drawerService.create<ModelLifeComponent, { value: string }, string>({
            nzTitle: 'Add new Model Life',
            nzContent: ModelLifeComponent,
            nzWidth: '320px'
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.getModelLife()
            }
        });
    }

}
