import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { AssetCustomFildService } from 'src/app/sdk/asset/services';
import { Custom_fild } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-custom-field',
    templateUrl: './custom-field.component.html',
    styleUrls: ['./custom-field.component.scss']
})
export class CustomFieldComponent implements OnInit {
    @Input() value = '';

    ngOnInit(): void {

    }


    constructor(
        private drawerRef: NzDrawerRef<string>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private assetCustomFildService : AssetCustomFildService
    ) { }

    close(): void {
        this.drawerRef.close(this.value);
    }
    example = '["a","b","c"]';

    customField: Custom_fild = {}
    addCustomFIeld() {

        if(this.customField.field_type == 'select' && this.customField.option_value != '')
        {
            try{
                JSON.parse(this.customField.option_value)
            }
            catch(e)
            {
                this.pesan.pesanWarningForm('Format Json tidak  valid!')
                return false;
            }
        }

        this.spinner.show();
        this.assetCustomFildService.postAssetCustomFilds(this.customField).subscribe(
            data => {
                this.spinner.hide()
                this.close()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Spesifikasi sudah ada');
            }
        )
    }

}
